<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false"     
            :listType="['List']"
            ModuleName="Asset"
            SubModuleCode="Asset"
            IdentityColumn="ASSET_CATALOGUE_ID"
             HeaderText="Check-Out List"
            :ListData="ProductData"
            :HeaderData="Headers"
            :widgets="widgets"
            :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords"      
            :SearchFields="leftSearchFields" 
            :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData"       
            :LegendArray="LegendArray"        
            :SortExp="SortExp"
            :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" 
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" 
            v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">  
                <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                
                    <a class="inactive-disabled">
                    <span v-if="data.row[data.column.COLUMN_NAME]=='Active'" class="btn d-inline-block text-center text-truncate btn-success">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-if="data.row[data.column.COLUMN_NAME]!='Active'" class="btn d-inline-block text-center text-truncate btn-danger">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </a>
             
              </template>
              <template v-if="data.column.COLUMN_NAME == 'Reject'">
                <td class="text-center single-action">
                <a href="javascript:;" id="Cancel" class="Cancel" data-name=" Dell Company_4" value="A20E9229-86CD-4BCA-81AB-1868BBF3D6C3" title="" data-toggle-tooltip="tooltip" data-original-title="Cancel">
                    <em class="fa fa-times-circle action-icon text-danger" title="Cancel"></em>
                </a>
               </td>
        </template>
            </template> 
    </tg-list>  

    </section>
</template>

<script>

import DataService  from '../../service/DataService'
import Schema from "../AssetRequisition/MyRequisitionsListSchema"
export default{
    data()
    {
        return{
            isLoading:false,    
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            LegendArray: [],
            ProductData:[],
            StatusListForListing:[],
            Headers:[{
                        "COLUMN_NAME": "AssetGroup",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "products",
                        "DISPLAY_ORDER": 1,
                        
                    },
                    {
                        "COLUMN_NAME": "IsAssigned",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "IsAssigned",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "UserName",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "UserName",
                        "DISPLAY_ORDER": 3
                    },
                    { 
                        "COLUMN_NAME": "CreatedAt",
                        "DATA_TYPE": "datetime",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "CreatedAt",
                        "DISPLAY_ORDER": 6
                    },
                    {
                        "COLUMN_NAME": "FromTime",
                        "DATA_TYPE": "datetime",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "FromTime",
                        "DISPLAY_ORDER": 4
                    },
                    
                    {
                        "COLUMN_NAME": "ToTime",
                        "DATA_TYPE": "datetime",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "ToTime",
                        "DISPLAY_ORDER": 5
                    },
                    { 
                        "COLUMN_NAME": "isMyRequisitions",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "isMyRequisitions",
                        "DISPLAY_ORDER": 7
                    },
                    {
                        "COLUMN_NAME": "StatusName",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Status Name",
                        "DISPLAY_ORDER": 8,
                        settings : {
                            isInSlot : true
                        }
                        
                    },
                    {
                        "COLUMN_NAME": "Reject",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Reject",
                        "DISPLAY_ORDER": 8,
                        settings : {
                            isInSlot : true
                        }
                        
                    },
                                          
            ],
            noRecord:true,
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            widgets: [],
            legendProgress:[{
                class:'',
                backgroundColor:'#17a2b8',
                name:'Draft'
            },
            {  class:'legend-Progress',
                backgroundColor:'',
                name:'Approved'
            },
            {
                class:'legend-pending',
                backgroundColor:'',
                name:'Pending'
            },
            {
                class:'',
                backgroundColor:'#fe6847',
                name:'SendForCorrection'
            },
            {
                class:'',
                backgroundColor:'#ee1313',
                name:'Rejected'
            }

            ],
            ProductData:[],
            LeadStatusList:[],
            PageSize:10,
            PageNumber:1,
            isPaged:1,
            searchCondition:'',
            AssetAssignmentData:[],
            ReturnProductPopup:false,
            catalogId:null,
            uniqueNames:null,
            IsAddCheckOutRequestList:false,
        }
    },
    created()
    {
        var ths=this;
       // Schema.bindleftCommonSearchdropdown(ths,'ASSETASSIGNMENT','IssueBy');
        ths.FetchData();
        this.LegendArray=[
            {
                class: 'legends_profile colorboxsquare',
                text: ('Expired Check-Out')
            },
        ]
      
    },
    methods:{
        FetchData:function(){            
            var vueObj = this;
           // vueObj.isLoading = true;   
            const params = this.getRequestParams(this.pageSize,this.pageNumber,this.sortBy,this.sortExp,this.catalogId,this.uniqueNames);         
            if (this.pageSize==undefined) {
                params.pageSize = vueObj.PageSize;  
            } 
            if (this.pageNumber==undefined) {
                params.pageNumber = vueObj.PageNumber;  
            } 
            
           //params.searchCondition = vueObj.searchCondition;
        //    if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
        //     else { vueObj.PageNumber = params.pageNumber = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            // if (!vueObj.isBlank(vueObj.searchCondition)) { srchCondition = srchCondition + vueObj.searchCondition; }
            // if (!vueObj.isBlank(vueObj.conditionForInstantSearch)) {
            //     srchCondition = srchCondition + ((!vueObj.isBlank(vueObj.searchCondition)) ? (" AND " + vueObj.conditionForInstantSearch) : vueObj.conditionForInstantSearch);
            // }
            // if (srchCondition != "") {
            //     params.searchCondition = srchCondition; 
            // }

            DataService.GetMyRequisitions(params).then(response=>{                
                vueObj.isLoading = false;
                if(response.data !=null){

                    console.log('res',response.data)
                    if(response.data.length>0){
                        vueObj.ProductData = response.data;
                        vueObj.TotalRecords = response.data[0].TotalRecord;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ProductData = [];
                        vueObj.TotalRecords = 0
                    }
                }else{
                    vueObj.noRecord = true;
                    vueObj.ProductData = [];                    
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () {vueObj.CheckBoxBootstrap()}, 100);                    
                setTimeout(function () {vueObj.ResponsiveDataTable('tablelistingdata')}, 500);
            });

            // var url=`search=&pageSize=15&pageNum=1&sortBy=&sortExp=&statusIds=&UniqueNames=&catalogId=`;
            // DataService.GetMyRequisitions(params).then(response=>{    
            //     debugger;          
            //     if(response.data!=null){
            //         if(response.data.length>0){
            //             this.ProductData=response.data;
            //         }
            //     }
           // });
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {               
                case "AddcheckOutrequest":
                    this.AddCheckOutRequest();
                    break;
            }
        },

        AddCheckOutRequest:function(){           
            this.$router.push({ name: "AssetAssignmentCheckOut" });
        },


        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };                
          
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
     
            this.pageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {   
             
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        getRequestParams: function ( pageSize,pageNum, sortBy, sortExp, isPaged,uniqueNames, catalogId) {
            
            let params = {};
            if (pageNum) { params["pageNumber"] = pageNum; }
            if (pageSize) { params["pageSize"] = pageSize; }        
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }    
            if (uniqueNames) { params["uniqueNames"] = uniqueNames; }    
            if (catalogId) { params["catalogId"] = catalogId; }    
          
           // if (isPaged) { params["isPaged"] = isPaged; }
           
            return params;
        },
        commonHeaderButtonClick(event){                                
            switch (event.callbackfunction) {                
                case "AddCheckOut":
                    this.AddCheckOutRequest();
                    break;
               
                   
            } 
        },
      



    }
}

</script>